
.setting_list .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.setting_list form{
    float: right;
    width: 100%;
}
.setting_list .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.setting_list .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.setting_list form label{
    float: right;
    margin-bottom: 15px;
    text-align: right;
}
.setting_list form .form-group{
    padding-bottom:15px;
}
.setting_list form .form-control{
    text-align: right;
    outline: none;
    box-shadow: none;
    background-color: #f5f5f5;
    border: 2px solid #cccccc;
    direction: rtl;
    padding: 10px 12px;
    height: 44px;
    border-radius: 6px;
}
.setting_list form textarea.form-control{
    height: 150px;
    text-align: left;
    direction: ltr;
    border-radius: 6px;
    resize: vertical;
    padding: 20px;
}
.setting_list .save{
    background: #f1f102;
    color: #000;
    box-shadow: none;
    outline: none;
    padding: 7px;
    font-size: 15px;
    float: right;
    margin-bottom: 50px;
    margin-right: 0;
    width: 170px;
    font-family: 'IRANSANS BOLD';
    box-shadow: 0px 5px 0px #d7d710;
    transition: all .2s ease-in;
}
.setting_list .save:hover{
    box-shadow: 0px 3px 0px #d7d710;
    transition: all .2s ease-in;
}
.loader{
    position: fixed;
    z-index: 9999;
}



