
.project_list .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.project_list .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.project_list .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.table{
    table-layout: fixed;
    direction: rtl;
}
.table>thead>tr>th,
.table>tbody>tr>td{
    text-align: center;
    vertical-align: middle
}
.table .remove_project,
.table .edit_project{
    margin-top: -5px;
}
.remove_project,
.edit_project{
    display: inline-block;
    background: #71e0ca;
    border: 1px solid transparent;
    border-radius: 6px;
    padding: 7px;
    outline: none;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    color: #fff;
    font-weight: 700;
    font-size: 15px;
    font-weight: bold;
    box-shadow: 0px 5px 0px #55c9b2;
    transition: all .2s ease-in;
}
.project_list .remove_project,
.project_list .edit_project{
    box-shadow: none;
    border-radius: 0;
    float: left;
    height: 40px;
    margin-top: -5px;
    margin-bottom: -5px;
    width: 40px;
    text-align: center;
}
.remove_project{
    background: #D91E18;
    box-shadow: 0px 5px 0px #C0392B;
}
.remove_project:hover{
    box-shadow: 0px 2px 0px #C0392B;
    transition: all .2s ease-in;
}
.edit_project:hover{
    box-shadow: 0px 2px 0px #55c9b2;
    transition: all .2s ease-in;
    text-decoration: none;
}
.loader{
    position: fixed;
    z-index: 9999;
}



