
#Login .container .row [class*=' col-'] {
	min-height: 550px;
	max-height: 550px;
}
#Login .shadow_box {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	overflow: hidden;
	-webkit-animation: ShowShadow .1s forwards 2s;
	animation: ShowShadow .1s forwards 2s;
}
@-webkit-keyframes ShowShadow {
0% {
		-o-box-shadow: 0 15px 40px rgba(0, 0, 0, 0);
		box-shadow: 0 15px 40px rgba(0, 0, 0, 0);
}
100% {
		-o-box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
		box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}
}
@keyframes ShowShadow {
0% {
		-o-box-shadow: 0 15px 40px rgba(0, 0, 0, 0);
		box-shadow: 0 15px 40px rgba(0, 0, 0, 0);
}
100% {
		-o-box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
		box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}
}
.remove_shadow {
	-webkit-animation: ShowShadow .1s reverse!important;
	animation: ShowShadow .1s reverse!important;
}
.fade_login_form {
	opacity: 1!important;
	-webkit-animation: MoveToCenter .5s forwards, HideFromCenter .3s forwards .6s !important;
	animation: MoveToCenter .5s forwards, HideFromCenter .3s forwards .6s !important;
}
@-webkit-keyframes HideFromCenter {
0% {
		opacity: 1;
}
100% {
		opacity: 0;
}
}
@keyframes HideFromCenter {
0% {
		opacity: 1;
}
100% {
		opacity: 0;
}
}
@-webkit-keyframes MoveToCenter {
0% {
		position: absolute;
		right: 0;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
100% {
		position: absolute;
		right: 50%;
		-webkit-transform: translate(50%, 0%);
		transform: translate(50%, 0%);
}
}
@keyframes MoveToCenter {
0% {
		position: absolute;
		right: 0;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
100% {
		position: absolute;
		right: 50%;
		-webkit-transform: translate(50%, 0%);
		transform: translate(50%, 0%);
}
}
.login_side {
	background-color: #fff;
	padding-top: 70px;
	opacity: 0;
	-webkit-animation: ShowFromNone .5s forwards 1s, StartFromCenter .5s forwards 1.5s;
	animation: ShowFromNone .5s forwards 1s, StartFromCenter .5s forwards 1.5s;
}
@-webkit-keyframes ShowFromNone {
0% {
		opacity: 0;
		position: absolute;
		top: 50%;
		right: 50%;
		-webkit-transform: translate(50%, -50%);
		transform: translate(50%, -50%);
}
100% {
		opacity: 1;
		position: absolute;
		top: 50%;
		right: 50%;
		-webkit-transform: translate(50%, -50%);
		transform: translate(50%, -50%);
}
}
@keyframes ShowFromNone {
0% {
		opacity: 0;
		position: absolute;
		top: 50%;
		right: 50%;
		-webkit-transform: translate(50%, -50%);
		transform: translate(50%, -50%);
}
100% {
		opacity: 1;
		position: absolute;
		top: 50%;
		right: 50%;
		-webkit-transform: translate(50%, -50%);
		transform: translate(50%, -50%);
}
}
@-webkit-keyframes StartFromCenter {
0% {
		opacity: 1;
		position: absolute;
		top: 50%;
		right: 50%;
		-webkit-transform: translate(50%, -50%);
		transform: translate(50%, -50%);
}
100% {
		opacity: 1;
		position: absolute;
		top: 0;
		right: 0%;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
}
@keyframes StartFromCenter {
0% {
		opacity: 1;
		position: absolute;
		top: 50%;
		right: 50%;
		-webkit-transform: translate(50%, -50%);
		transform: translate(50%, -50%);
}
100% {
		opacity: 1;
		position: absolute;
		top: 0;
		right: 0%;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
}
.login_bg {
	background-image: url('../assets/bg.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	opacity: 0;
	-webkit-animation: StartFromCenterBack .5s forwards 1.6s;
	animation: StartFromCenterBack .5s forwards 1.6s;
}
@-webkit-keyframes StartFromCenterBack {
0% {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
}
100% {
		opacity: 1;
		position: absolute;
		top: 0;
		left: 0;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
}
@keyframes StartFromCenterBack {
0% {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
}
100% {
		opacity: 1;
		position: absolute;
		top: 0;
		left: 0;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
}
.fade_login_bg {
	opacity: 1;
	position: absolute;
	left: 0;
	-webkit-animation: HideFromLeft .3s forwards .2s;
	        animation: HideFromLeft .3s forwards .2s;
}
@-webkit-keyframes HideFromLeft {
0% {
		opacity: 1;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
100% {
		opacity: 0;
		width: 30%;
		-webkit-transform: translate(50%, 0%);
		transform: translate(50%, 0%);
}
}
@keyframes HideFromLeft {
0% {
		opacity: 1;
		-webkit-transform: translate(0%, 0%);
		transform: translate(0%, 0%);
}
100% {
		opacity: 0;
		width: 30%;
		-webkit-transform: translate(50%, 0%);
		transform: translate(50%, 0%);
}
}
.login_logo {
	width: 130px;
	height: auto;
	display: block;
	margin: 0 auto;
}
.login_side h2 {
	font-size: 1.1em;
	text-align: center;
	color: #999;
}
.login_form {
	width: 180px;
	display: block;
	margin: 70px auto;
}
.login_form .form-control {
	border: 0px;
	direction: rtl;
	padding: 30px 0;
	padding-right: 40px;
	color: #555;
	font-size: 13px;
	box-shadow: none;
	outline: none;
	border-radius: 0;
	position: relative;
	background-repeat: no-repeat;
	background-position: right center;
	background-size: 25px;
	-moz-box-sizing: initial;
}
.login_form .form-control:nth-child(1) {
	border-bottom: 1px solid #cccccc;
	background-image: url('../assets/user.svg');
}
.login_form .form-control:nth-child(2) {
	background-image: url('../assets/pass.svg');
}
.login_form .btn {
	background-color: #E1E1E1;
	color: #8E8E8E;
	font-size: 14px;
	font-weight: bold;
	margin: 50px auto;
	border-radius: 6px;
	position: relative;
	outline: none;
	box-shadow: none;
}
.login_form .btn:hover,
.login_form .btn:focus,
.login_form .btn:active {
	background-color: #cecece;
}
.login_form .btn:after {
	content: '';
	position: absolute;
	left: 20px;
	top: 35%;
	width: 10px;
	height: 10px;
	background-image: url('../assets/left-arrow.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 20px;
}
.hide_login_bg {
	-webkit-animation: HideBg .5s forwards;
	animation: HideBg .5s forwards;
}
@-webkit-keyframes HideBg {
0% {
		opacity: 1;
}
100% {
		opacity: 0;
}
}
@keyframes HideBg {
0% {
		opacity: 1;
}
100% {
		opacity: 0;
}
}
.loader {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	text-align: center;
	background-color: rgba(0, 0, 0, 0.3);
	z-index: 999;
}
.loader svg {
	position: absolute;
	left: 50%;
	top: 50%;
	height: 50px;
	width: 50px;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}
.error_input{
	border-bottom:1px solid #D91E18!important;
}



