
.project .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.project form{
    float: right;
    width: 100%;
}
.project .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.project .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.project form label{
    float: right;
    margin-bottom: 15px;
    text-align: right;
}
.project form .form-group{
    padding-bottom:15px;
}
.project form .form-control{
    text-align: right;
    outline: none;
    box-shadow: none;
    background-color: #f5f5f5;
    border: 2px solid #cccccc;
    direction: rtl;
    padding: 10px 12px;
    height: 44px;
    border-radius: 6px;
}
.project form textarea.form-control{
    height: 150px;
    padding:20px;
    text-align: left;
    direction: ltr;
    border-radius: 6px;
    resize: vertical;
    padding: 20px;
}
.quill-editor{
    padding: 0;
}
.ql-container{
    background-color: #f5f5f5;
    border-radius: 0 0 6px 6px;
}
.ql-editor.ql-blank::before{
    right: 10px;
}
.ql-toolbar.ql-snow{
    border-radius: 6px 6px 0 0;
    background-color: #f5f5f5;
    direction: rtl;
}
.ql-container .ql-editor {
    min-height: 10em;
    padding-bottom: 1em;
    max-height: 15em;
    font-family: "IRANSANS";
}
.ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg{
    left: 0;
    right: auto!important;
}
.project figure{
    margin: 0;
    padding: 0;
    position: relative;
    max-height: 150px;
    float: right;
    display: block;
    margin-left: 15px;
    margin-bottom: 15px;
}
.project figure:hover .remove_img{
    opacity: 1;
    transition: all .2s ease-in;
}
.project .remove_img{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
    text-align: center;
    background: rgba(255, 255, 255, 0.45);
    transition: all .2s ease-in;
}
.project .remove_img:after{
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    height: 35px;
    width: 60px;
    border-radius: 6px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: #fff;
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
    background-image: url(data:image/svg+xml;utf8;base64,)
}
.project .thumbnail{
    border-radius: 5px;
    border:0;
    height: auto;
    min-height: 100px;
    min-width: 170px;
    max-width: 170px;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
    max-height: 100px;
    padding: 0;
    background: transparent;
    float: right;
    margin-bottom: 0;
}
.project .save{
    background: #f1f102;
    color: #000;
    box-shadow: none;
    outline: none;
    padding: 7px 50px;
    font-size: 15px;
    float: right;
    margin-bottom: 50px;
    margin-right: 10px;
    width: 170px;
    font-family: 'IRANSANS BOLD';
    box-shadow: 0px 5px 0px #d7d710;
    transition: all .2s ease-in;
}
.project .save:hover{
    box-shadow: 0px 3px 0px #d7d710;
    transition: all .2s ease-in;
}
.loader{
    position: fixed;
    z-index: 9999;
}
.progress{
    float: right;
    width: 200px;
    margin-right: 20px;
    height: 20px;
    margin-top: 10px;
    display: none;
    box-shadow: none;
    border: 1px solid #ebebeb;
    position: relative;
}
.progress::before {
    content: 'در حال بارگزاری تصویر';
    background: transparent;
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    z-index: 99999;
    text-align: center;
    font-size: 13px;
}
.progress-bar{
    box-shadow: none;
    background-color: #f1f102;
    color: #444;
    line-height: 1.5;
    border-radius: 3px 0 0 3px;
}
.padding-right-none{
    padding-right: 0px;
}



