
#menu{
    position: fixed;
    top: 40px;
    right: 40px;
    width: auto;
    z-index: 9;
    z-index: 999;
}
#menu nav{
    display: block;
    background-color: #3a3a3a;
    border-radius: 6px;
}
#menu nav .nav_header{
    padding: 15px;
    direction: rtl;
    background-color: #363636;
    border-radius: 6px;
}
#menu .menu_title{
    color: #fff;
    font-size: 1em;
    font-family: 'IRANSANS BOLD';
    margin-right: 35px;
    margin-top: 0;
    margin-bottom: 0;
    cursor: pointer;
}
#menu .menu_icon{
    float: right;
    height: 25px;
    width: 22px;
    cursor: pointer;
    margin-top: -5px;
}
#menu .menu_icon path,
#menu .menu_icon line{
    stroke: #fff;
    stroke-width: 7px;
    fill: #fff;
}
#menu .nav_list{
    display: block;
    list-style: none;
    padding-left: 0;
    max-height: 0;
    opacity: 0;
    transition: all .6s ease;
    overflow: hidden;
}
#menu .nav_list.show{
    max-height: 1000px;
    opacity: 1;
    transition: all .6s ease;
    padding-bottom: 10px;
    overflow: visible;
}
#menu .nav_list li{
    background-color: transparent;
    position: relative;
    transition: background-color .3s ease;
}
#menu .nav_list li a,
#menu .nav_list li span{
    color: #cccccc;
    font-size: 13px;
    text-align: right;
    display: block;
    padding: 10px;
    padding-right: 15px;
    text-decoration: none;
    background-color: transparent;
    font-family: 'IRANSANS BOLD';
    transition: all .3s ease;
    cursor: pointer;
}
#menu .nav_list li a:hover,
#menu .nav_list li span:hover{
    color: #ffff00;
    background-color: #404040;
    transition: all .3s ease;
}
#menu .nav_list li a:last-child{
    border-radius:0 0 6px 6px;
}
#menu .nav_list .arrow_dropdown{
    height: 10px;
    width: 10px;
    position: absolute;
    left: 15px;
    top: 15px;
    transition: all .3s ease;
    cursor: pointer;
}
#menu .nav_list .arrow_dropdown.rotate{
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    transition: all .3s ease;
}
#menu .nav_list .child_menu{
    padding: 0 15px;
    position: relative;
    list-style: none;
    background-color: #404040;
    transition: all .3s ease;
    max-height: 0;
    min-height: 0;
    opacity: 0;
}
#menu .nav_list .child_menu:before{
    content: '';
    position: absolute;
    right: 15px;
    top: 0;
    bottom: 0;
    width: 3px;
    background-color: #ffff00;
    z-index: 999;
}
#menu .nav_list .child_menu.show_dropdown{
    min-height: 75px;
    opacity: 1;
    transition: all .3s ease;
}
#menu .nav_list .child_menu li a:hover{
    background-color: transparent;
}



